.table-wrapper{
	width: 100%;
	.react-bootstrap-table{
		width: 100%;
		overflow-x: scroll !important;

	}
	.header-selects.header-selects-year{
		width: 20%;
		margin-bottom: 30px;
	}
}

.footer-table{
	text-align: center;
	margin-top: 20px;
	button{
		margin-right: 10px;
	}
}

.universal__table_panel{
	width: 100%;
	.react-bootstrap-table{
		width: 100%;
		overflow-x: scroll !important;

	}
	&_title{
		text-align: center;
		font-size: 1.3em;
		font-family: RobotoBold;
		margin-bottom: 10px;
	}
	.panel_bottom{
		display: flex;
		flex-direction: row;
		gap: 10px;
		justify-content: center;
	}
}
.table{
	min-width: 1000px;
	// overflow: scroll !important;
}