@import "vars";
@import "fonts";

.SystemSettings{
	display: flex;
	width: 100%;
	flex-wrap: wrap;
	gap: 2%;
	.slot{
		flex: 0 0 48%;
		padding: 25px 15px;
		border-radius: 5px;
		background-color: white;
		@include bs(10px);
		width: 50%;
		margin-bottom: 20px;
		.react-bootstrap-table{
			.table{
				min-width: 250px;
			}
		}
	}
	.slot_backups{
		h2{
			font: 1.5em RobotoBold;
			text-align: center;
		}
		p{
			font: 1.3em RobotoRegular;
			padding: 7px;
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		&_list{
			margin-top: 30px;
			position: relative;
			li{
				// background-color: red;
				padding: 7px 10px;
				margin-bottom: 5px;
				border-radius: 3px;
				display: flex;
				justify-content: space-between;
				align-items: center;
				border: 1px solid rgb(216, 216, 216);
				&:hover{
					background-color: rgb(230, 230, 230);
				}
			}
			&::before{
				content: "Название бэкапа";
				position: absolute;
				padding-left: 10px;
				color: rgb(161, 161, 161);
				top: -20px;
			}
		}
	}
	.pagination{
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		margin: 20px 0;
		select{
			height: 40px;
			width: 30%;
		}
	}
}