@import "fonts";
body{
	height: 100vh;
}
#root{
	font-family: RobotoLight;
	width: 100%;
	// height: 100%;
	display: flex;
	flex-direction: row;
}

.FLabel_line{
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	margin-bottom: 7px;
	width: 40%;
	min-width: 200px;
}
.FLabel_line.mark_bold{
	font-family: RobotoBold;
}

#root .notification-container{
	bottom: 10px !important;
	top: auto;
}

.iFXJzb table th input, .iFXJzb table td input {
	width: 120px;
}

.sr-only{
	display: none;
}

.Home{
	h1{
		font-size: 1.5em;
		font-family: RobotoBlack;
		margin-bottom: 20px;
	}
	.current_week{
		font-family: RobotoBold;
		font-size: 1.2em;
	}
}


._mark{
	color: white;
	font-family: RobotoBold;
	border-radius: 3px;
	padding: 3px;
}

.mark-admin{
	background-color: rgb(192, 25, 25);
}
.mark-moderator{
	background-color: rgb(59, 173, 14);
}
.mark-editor{
	background-color: rgb(14, 56, 173);
}


// Pagination tables

.react-bs-table-sizePerPage-dropdown{
	.dropdown-menu{
		li{
			padding: 0;
			a{
				display: inline-block;
				width: 100%;
				padding: 7px 5px 7px 20px;
				text-decoration: none;
				// color: #000;
				font-family: 1em RobotoRegular;
			}
		}
	}
}
.pagination.react-bootstrap-table-page-btns-ul{
	display: flex;
	justify-content: flex-end;
}