@import "vars";
@import "fonts";

// -----------------------------------------------------------------------------
// ----------------------------- STYLES FOR STATIC -----------------------------
// -----------------------------------------------------------------------------

.sidebar{
	position: relative;
	.icon_bars{
		position: absolute;
		top: 20px;
		left: 20px;
		font-size: 1.5em;
		display: none;
	}

}

// -----------------------------------------------------------------------------
// ------------------------------- MEDIA REQUESTS ------------------------------
// -----------------------------------------------------------------------------

@media screen and (min-width: 781px) {
	button.apply_order_number{
		display: none;
	}
	button.apply_all_assembly_and_entering_block{
		display: none;
	}
}


@media screen and (max-width: 780px) {
	.sidebar{
		.icon_bars{
			display: block;
		}
		.sidebar_content.responsive_noactive{
			display: none;
		}
		.sidebar_content.responsive_active{
			position: absolute;
			top: 0;
			left: 0;
			z-index: 999;

			display: block;
			width: 100vw;
			height: 100vh;
			background-color: $color-main;
			overflow: hidden;
		}
	}

	main{
		padding: 60px 5px;
		.Order{
			padding: 20px 5px !important;
			// overflow: hidden;
			.top_panel{
				justify-content: center !important;
				flex-direction: column !important;
				width: 100%;
				.order_form_user{
					margin-right: 0;
					width: 100%;
					.mb-12.form-floating{
						width: 100%;
						input{
							width: 100% !important;
						}
					}
					margin-bottom: 15px;
				}
				.order_form_additional{
					width: 100%;
					.select_site{
						width: 100% !important;
					}
					.within{
						display: flex;
						gap: 10px;
						margin-bottom: 10px;
						*{
							flex: 30% !important;
							margin: 0 !important;
						}
						button.apply_order_number{
							display: block;
							height: 40px;
						}
						.mb-12.form-floating{
							width: 100%;
							input{
								width: 100% !important;
							}
							label{
								font-size: 0.8em;
							}
						}
					}
					.form-floating{
						width: 100% !important;
						input[name="date_deliver"]{
							width: 100% !important;
						}

					}
				}
			}
		}
		.order_panel{
			.oreder_panel_main_info{
				display: flex;
				flex-direction: column !important;
				gap: 7px;
				*{
					width: 100% !important;
				}
			}
		}
		.additional_info{
			// padding: 15px 30px !important;
			// width: 95%;
			.form-check{
				// display: flex;
				// justify-content: center;
				// padding: 0 20px !important;
				// width: 90%;
				margin: 5px 0;
			}
		}
	}

	.Invoice-wrapper{

		.Invoice{
			padding: 20px 5px !important;
		}
		.panel-bottom{
			padding-right: 10px !important;
		}
	}

	.SystemSettings{
		display: flex;
		width: 100%;
		flex-direction: column !important;
		.slot{
			width: 100%;
		}
	}
}